.header {
  color: #B3B3BA;
  padding-top: 20px;
}
.header__title {
  position: relative;
  display: inline;
  font-size: 19px;
  line-height: 22px;
  color: white;
  padding: 13px;
}
.header__title::before {
  position: absolute;
  left: 0;
  content: url("./../images/header/header-title-mobile-before.svg");
}
.header__title::after {
  position: absolute;
  right: -11px;
  content: url("./../images/header/header-title-mobile-after.svg");
}
.header__language-btn {
  color: #B3B3BA;
  border-radius: 8px;
  padding: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.header__language-btn--active {
  background-color: #F0145A;
  color: #ffffff;
}
.header__language-btn:hover {
  background-color: #EDEDF7;
  color: #B3B3BA;
}

.tel-number {
  font-weight: 500;
}

.nav-link {
  color: white;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  padding-bottom: 0;
  border-bottom: 2px solid transparent;
}
.nav-link:hover {
  color: #F0145A;
}
.nav-link.active {
  border-bottom: 2px solid white;
  pointer-events: none;
}

@media (min-width: 992px) {
  .logo {
    position: absolute;
    top: 50px;
    left: 10px;
  }

  .header {
    font-size: 16px;
    color: #ffffff;
  }
  .header__title {
    font-size: 38px;
    line-height: 45px;
    padding: 13px;
    margin-left: 10px;
  }
  .header__title::before {
    left: -8px;
    content: url("./../images/header/header-title-desktop-before.svg");
  }
  .header__title::after {
    right: -30px;
    content: url("./../images/header/header-title-desktop-after.svg");
  }
  .header__language-btn {
    color: #ffffff;
  }

  .bullet {
    font-weight: bold;
  }
}
.hero {
  position: relative;
  background: linear-gradient(42.01deg, #323264 14.33%, #323264 51.39%, #7A425E 87.53%);
}
.hero::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  background: linear-gradient(180deg, rgba(50, 50, 100, 0) 16.67%, #323264 100%);
  pointer-events: none;
}

.hero__desc {
  position: relative;
  margin-top: 100px;
  padding-top: 55px;
  min-height: 610px;
  background-image: url("./../images/hero/hero-long-leg.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}
.hero__desc__title {
  position: relative;
  color: #ffffff;
  font-size: 38px;
  line-height: 45px;
  text-align: center;
  z-index: 2;
  max-width: 500px;
}
.hero__desc__img {
  position: absolute;
  top: 0;
  left: 0;
}
.hero__desc__shade {
  position: absolute;
}

.hero__btn-scroll--down {
  border: 1px solid white;
  width: 42px;
  height: 42px;
  background-image: url("./../images/hero/hero-btn-scroll-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  background-color: transparent;
  position: absolute;
  bottom: 25px;
  cursor: pointer;
  z-index: 2;
}

@media (min-width: 992px) {
  .hero__desc {
    background-image: url("./../images/hero/hero-long-leg.svg"), url("./../images/hero/hero-long-leg-background.svg");
    max-width: 100%;
    background-repeat: no-repeat;
    background-size: auto;
    background-position: bottom;
    padding-top: 170px;
  }
