.publications__card {
  background-color: white;
  border-radius: 8px;
  margin-bottom: 40px;
}
.publications__card__title {
  color: #24A3FF;
  font-size: 18px;
  line-height: 21px;
  padding: 20px 20px 10px 20px;
  margin: 0;
}
.publications__card__date {
  color: #B3B3BA;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: 0 20px 20px 20px;
  margin: 0;
}
.publications__card__text {
  position: relative;
  font-size: 16px;
  line-height: 22px;
  padding: 0 20px 20px 20px;
  margin-top: 20px;
}
.publications__card__text::before {
  content: "";
  position: absolute;
  left: 20px;
  right: 20px;
  top: -20px;
  border: 2px solid #E6E6F0;
}
.publications__card__img {
  width: 100%;
}

@media (min-width: 992px) {
  .publications {
    max-height: 900px;
    overflow: hidden;
  }
  .publications__btn {
    width: 250px;
    height: 50px;
    background-color: #24A3FF;
    box-shadow: 0px 0px 20px rgba(36, 163, 255, 0.5);
    border: none;
    border-radius: 12px;
    font-size: 14px;
    line-height: 17px;
    color: white;
    font-weight: 500;
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
  }
  .publications::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 200px;
    background: linear-gradient(180deg, rgba(245, 245, 250, 0) 0%, #F5F5FA 86.5%);
    pointer-events: none;
  }
}
.footer {
  padding-bottom: 50px;
  background-color: #F5F5FA;
  text-align: center;
}
.footer__container {
  position: relative;
  padding-top: 20px;
}
.footer__container.with-border::before {
  content: "";
  position: absolute;
  left: 10px;
  right: 10px;
  top: 0;
  bottom: 0;
  border-top: 4px solid #E6E6F0;
  pointer-events: none;
}
.footer__address {
  color: #323264;
  font-weight: 600;
}
.footer__map {
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  color: #24A3FF;
  font-weight: 600;
}
.footer__map:hover {
  color: #F0145A;
}
.footer__phone {
  color: #323264;
  font-weight: 600;
}
.footer__email a {
  color: #24A3FF;
  font-size: 16px;
  line-height: 19px;
  text-decoration: none;
  font-weight: 500;
}
.footer__email a:hover {
  color: #F0145A;
}
.footer__copyright {
  color: #B3B3BA;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
}
.footer .bullet {
  color: #B3B3BA;
  font-weight: 800;
  padding-left: 10px;
  padding-right: 10px;
}
.footer .facebook-link:hover svg path {
  fill: #F0145A;
}

@media (min-width: 992px) {
  .footer {
    text-align: start;
  }
  .footer__container {
    padding-top: 30px;
  }
  .footer__copyright {
    text-align: end;
  }
  .footer__content-row {
    display: flex;
    align-items: center;
  }
}